import PropTypes from 'prop-types';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as yup from 'yup';
import './OrderForm.scss';

const initialValues = {
  name: '',
  phone: '',
  comment: '',
};

const schema = yup.object().shape({
  name: yup
    .string()
    .matches(/\D/g, "–ü–æ–ª–µ –Ü–º'—è –Ω–µ –º–æ–∂–µ –º—ñ—Å—Ç–∏—Ç–∏ —Ü–∏—Ñ—Ä–∏")
    .min(2, "–ü–æ–ª–µ –Ü–º'—è –ø–æ–≤–∏–Ω–Ω–æ –º—ñ—Å—Ç–∏—Ç–∏ –º—ñ–Ω—ñ–º—É–º 2 —Å–∏–º–≤–æ–ª–∏")
    .required("–û–±–æ–≤'—è–∑–∫–æ–≤–µ –ø–æ–ª–µ"),
  phone: yup
    .string()
    .matches(
      // eslint-disable-next-line no-useless-escape
      /^((0|\+3|\+38|38))?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/g,
      '–í–≤–µ–¥—ñ—Ç—å –∫–æ—Ä–µ–∫—Ç–Ω–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É, –Ω–∞–ø—Ä–∏–∫–ª–∞–¥ "0991112233" –∞–±–æ "+380991112233"'
    )
    .required("–û–±–æ–≤'—è–∑–∫–æ–≤–µ –ø–æ–ª–µ"),
});

const OrderForm = ({ onSubmit }) => {
  const handleSubmit = async (values, actions) => {
    await onSubmit(values);
    actions.setSubmitting(false);
    actions.resetForm();
  };

  return (
    <>
      <Formik
        onSubmit={handleSubmit}
        initialValues={initialValues}
        validationSchema={schema}
      >
        {({ isSubmitting }) => (
          <Form className="order-form" autoComplete="off">
            <label className="fieldset">
              <span className="label">
                –Ü–º'—è<span className="required">*</span>
              </span>
              <Field type="text" name="name" className="input" />
              <ErrorMessage
                component="div"
                name="name"
                className="error-message"
              />
            </label>
            <label className="fieldset">
              <span className="label">
                –¢–µ–ª–µ—Ñ–æ–Ω<span className="required">*</span>
              </span>
              <Field type="tel" name="phone" className="input" />
              <ErrorMessage
                component="div"
                name="phone"
                className="error-message"
              />
            </label>
            <label className="fieldset">
              <span className="label">–ö–æ–º–µ–Ω—Ç–∞—Ä</span>
              <Field
                as="textarea"
                name="comment"
                className="textarea"
                style={{ width: '100%', height: '100px' }}
              />
            </label>
            <button
              type="submit"
              className="submit-btn"
              disabled={isSubmitting}
            >
              –û—Ñ–æ—Ä–º–∏—Ç–∏ –∑–∞–º–æ–≤–ª–µ–Ω–Ω—è
            </button>
          </Form>
        )}
      </Formik>
    </>
  );
};

OrderForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
};

export default OrderForm;
